<template>
  <div class="profile" itemscope itemtype="http://schema.org/Person">
    <div class="author__avatar">
      <img
        :src="imageUrl + user[0].image"
        class="avatar"
        :alt="user[0].website"
        itemprop="image"
      >
    </div>
    <div class="author__content">
      <h3 class="author__name" itemprop="name">{{ user[0].name }}</h3>
      <p class="author__bio" itemprop="description">{{ user[0].bio }}</p>
    </div>

    <div class="author__urls-wrapper">
      <ul class="fa-ul">
        <li>
          <a :href="'http://' + user[0].website" itemprop="url">
            <i class="fa fa-li fa-fw fa-link" aria-hidden="true"></i>
            {{ user[0].website }}
          </a>
        </li>
        <li>
          <a :href="'mailto:' + user[0].email">
            <meta itemprop="email" :content="user[0].email">
            <i class="fa fa-li fa-fw fa-envelope-square" aria-hidden="true"></i>
            {{ user[0].email }}
          </a>`
        </li>
        <li>
          <a :href="'https://www.linkedin.com/in/' + user[0].linkedin" itemprop="sameAs">
            <i class="fab fa-li fa-fw fa-linkedin" aria-hidden="true"></i> LinkedIn
          </a>
        </li>
        <li>
          <a :href="'https://github.com/' + user[0].github" itemprop="sameAs">
            <i class="fab fa-li fa-fw fa-github" aria-hidden="true"></i> GitHub
          </a>
        </li>
        <li>
          <a :href="'https://dev.to/' + user[0].devto" itemprop="sameAs">
            <i class="fab fa-li fa-fw fa-dev" aria-hidden="true"></i> Dev.to
          </a>
        </li>
      </ul>
    </div>
  </div>
</template>


<style type="text/css">
.avatar {
  vertical-align: middle;
  max-width: 110px;
  border-radius: 50%;
}
.profile {
  border-left: 1px solid #f2f2f2;
  padding-left: 20px;
  padding-bottom: 10px;
  text-align: left;
}
</style>

<script>
// Import Libraries
const axios = require("axios");

function buildUrl(username) {
  return "http://localhost:8000/api/user?username=" + username;
}

export default {
  name: "Profile",

  data() {
    return {
      imageUrl: "http://tony-hammack.s3-website.us-east-2.amazonaws.com/",
      user: []
    };
  },
  created() {
    // fetch the data when the view is created and the data is
    // already being observed
    this.fetchData();
  },
  methods: {
    fetchData() {
      const username = "tonus";
      let url = buildUrl(username);
      axios
        .get(url)
        .then(response => {
          this.user = response.data;
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>``
